var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"text_wrap"},[_c('h1',[_vm._v("独立の脅威")]),_c('p',[_vm._v("悲願のユダヤ人国家を手にすることになったユダヤ人は大いに喜んだ。「ユダヤ人の歴史でこれを超える偉業はない」とまで言われていたほど、ユダヤ人国家の建国は喜ばれた。 "),_c('br'),_vm._v("こうして独立を手にすることとなったユダヤ人国家、イスラエルだったが、独立する前からこの国家に脅威が迫っていた。国連分割案は可決されたとはいえ、イスラエルの周辺国はほぼすべてが分割案に反対しており、イスラエルの存在を認めていなかった。 "),_c('br'),_vm._v("独立直後からイスラエルはその独立を脅かす脅威に晒されることとなった。 ")])])}]

