var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"text_box"},[_c('h1',[_vm._v("新たな懸念")]),_c('p',[_vm._v("パレスチナ地方がオスマン帝国の支配下だった時代から、パレスチナには既にユダヤ人は少数ながら居住していた。しかし20世紀に入るとその数は増加の一途をたどり、イギリスの支配がはじまると増加傾向は更に強まった。イギリスの支配下だった30年余りでユダヤ人人口は実に7.5倍になった。 "),_c('br'),_vm._v("入植してきたユダヤ人は更にその居住地を広げ、この30年でユダヤ人はアラブ人にとって長年の居住地を奪いに来た厄介者として見るようになった。 ")]),_c('div',{attrs:{"id":"population"}},[_vm._v(" 83794 ")]),_c('div',{staticClass:"graph"},[_c('div',{staticClass:"jew_graph"}),_c('div',{attrs:{"id":"arab_graph"}}),_c('div',{attrs:{"id":"disc_text"}},[_vm._v(" 11"),_c('span',{staticStyle:{"font-size":"25px"}},[_vm._v("%")])]),_c('p',[_vm._v("ユダヤ人人口")])])])}]

