<style scoped>
#section{
    height: 400vh;
    position: relative;
    background-color: #3a3a3a;
}
.sticky_box{
    position: sticky;
    top: 10vh;
    float: right;
    margin-right: 5vw;
    width: 50%;
    height: 75vh;
}
.left_box{
    position: absolute;
    /* display: block; */
    top: 0;
    left: 10%;
    width: 50%;
    height: 100%;
}
.text_box{
    /* position: absolute; */
    margin-top: 20vh;
    margin-left: 10vw;
    width: 60%;
    height: 80vh;
}
.text_box h1{
    font-size: 80px;
    border-bottom: 3px solid #e1e1e1;
    margin-bottom: 25px;
}
svg{
    height: 700px;
    stroke: #fff;
    stroke-width: 3px;
}
.palestine{
    fill:#0e8316;
}
.israel{
    fill:#0071ba;
}
#stage2, #stage3, #stage4{
    opacity: 0;
}
#logging{
    position: sticky;
    opacity: 0.5;
    top: 0;
}
.pointer{
    position: sticky;
    top: 45vh;
    width: 10%;
    z-index: 10;
}
.pointer.to_right{
    float: right;
    right: 3vw;
}
.pointer.to_left{
    float: left;
    left: 3vw;
}
.pointer a{
    text-decoration: none;
    font-size: 500%;
    height: 100%;
}
.pointer.to_left a{
    color: #0071ba;
}
.pointer.to_right a{
    color: #0e8136;
}
.router_title{
    margin: 0;
    font-size: 20px;
    opacity: 0;
    transform: translateY(20px);
    transition: all .5s ease-in-out;
}
.router_show{
    opacity: 1;
    transform: translateY(0px);
}
</style>
<script>
export default{
    methods:{
        getRatio(el){
            var par = el.parentNode;
            var ratio = (el.scrollTop || par.scrollTop) / (par.scrollHeight - par.clientHeight);
            // document.getElementById("logging").innerHTML = ratio;
            var thresh = 0.6;
            return (ratio >= thresh) ? (ratio - thresh)/(1 - thresh) : 0;
        },
        scrollAnimation(){
            var ratio = this.getRatio(document.body);
            var stage = Math.ceil(ratio * 3);
            if(stage <= 0){
                stage = 1;
            }
            var oldObj = document.getElementById("stage"+stage);
            var newStage = stage + 1;
            var newObj = document.getElementById("stage"+newStage);
            // console.log(ratio);
            
            oldObj.style.opacity = 1 - (3 * ratio - (stage - 1));
            newObj.style.opacity = 3 * ratio - (stage - 1);
            
        },
        hovereffect(event){
            var trg = event.target.previousSibling;
            trg.classList.add("router_show");
            // console.log(trg);
        },
        hoverouteffect(event){
            var trg = event.target.previousSibling;
            trg.classList.remove("router_show");
            // console.log(trg);
        }
    },
    created(){
        document.addEventListener("scroll",this.scrollAnimation);
    }
}
</script>