</template>
<style scoped>
    #fix2{
        height: 100vh;
        display: block;
        /* margin-bottom: 200px; */
    }
    .box{
        position: relative;
    }
    .arab_coun{
        fill: #0e8316;
        visibility: visbile;
        transition: opacity .5s ease-in-out, visibility .5s ease-in-out;
    }
    .inde{
        fill:#0e8316;
    }
    .bri{
        fill:blue;
    }
    .fre{
        fill: red;
    }
    .inde, .bri, .fre{
        stroke: #fff;
        stroke-width: 2px;
        stroke-dasharray: 3000;
        stroke-dashoffset: 3000;
        opacity: 0;
        visibility: hidden;
        transition: opacity .5s ease-in-out, stroke-dashoffset .5s ease-in-out, visibility .5s ease-in-out;
    }
    .show{
        opacity: 1;
        stroke-dashoffset: 0;
        visibility: visible;
    }
    .hide{
        opacity: 0;
        visibility: hidden;
    }
    .map_box{
        height: 80vh;
        position: absolute;
        top: 3vh;
        left: 55vw;
    }
    #arab_pen{
        height: 100%;
    }
    .text_box{
        position: absolute;
        top: 10vh;
        left: 10vw;
        width: 35%;
    }
    .text_box h1{
        font-size: 80px;
        border-bottom: 3px solid #0e8316;
        margin-bottom: 25px;
    }
    .text_box p{
        width: 80%;
        margin: auto;
    }
    .legend_box{
        position: absolute;
        top: 65vh;
        left: 43vw;
        width: 20%;
        margin: auto;
        font-size: 42px;
        text-align: left;
    }
    #independent{
        height: 30px;
    }
    .flag{
        stroke: yellow;
        stroke-width: 5px;
    }
    #icon{
        position: absolute;
        height: 50px;
        width: 250px;
        background-color: #eee;
        border: 3px solid #333;
        border-radius: 5px;
        transform: translate(0%,-110%);
        display: none;
        font-size: 25px;
        vertical-align: center;
        color: #333;
    }
    #icon p{
        margin-top: 5px;
    }
    button{
        width: 250px;
        height: 70px;
        padding: 15px;
        border-radius: 10px;
        cursor: pointer;
        font-size: 25px;
        transition: all .2s ease-in-out;
        position: absolute;
        top: 65vh;
        left: 20.5vw;
    }
    button:hover{
        background-color: #3a3a3a;
        color: #e1e1e1;
    }
    .text_animation-leave-active{
        transition: all 0s;
    }
    .text_animation-enter-active{
        transition: all .7s ease-in-out;
    }
    .text_animation-enter, .text_animation-leave-to{
        opacity: 0;
    }
    .text_animation-enter{
        transform: translateY(50px);
    }
</style>
<script>
export default{
    data(){
        return{
            flag: false,
            text_flag: true,
            countries: [
                {id: 'SAU', name: 'サウジアラビア'},
                {id: 'LEB', name: 'レバノン委任統治領'}, 
                {id: 'SYR', name: 'シリア委任統治領'},
                {id: 'QTA', name: '休戦オマーン'},
                {id: 'UAE', name: '休戦オマーン'},
                {id: 'KUW', name: 'クウェート保護領'},
                {id: 'PAL', name: 'パレスチナ委任統治領'},
                {id: 'OMA', name: 'オマーン'},
                {id: 'IRQ', name: 'イラク委任統治領'},
                {id: 'YEM', name: 'イエメン'},
                {id: 'YMD', name: 'アデン保護領'},
                {id: 'JOR', name: 'ヨルダン'},
            ],
        }
    },
    methods: {
        show_map(){
            var cls = document.querySelectorAll(".inde, .bri, .fre");
            this.flag = !this.flag;
            var old = document.querySelectorAll("#arab_pen .arab_coun");

            if(this.flag){
                cls.forEach(element =>{
                    element.classList.add("show");
                    old[0].classList.add("hide");
                })
            } else {
                cls.forEach(element =>{
                    element.classList.remove("show");
                    old[0].classList.remove("hide");
                })
            }
            this.text_flag = !this.text_flag;
        },
        mouseOverEffect(event){        
            var icon = document.getElementById("icon");
            // console.log(icon.children);
            icon.style.display = "block"
            var width = event.offsetX - icon.style.width/2;
            var target = event.target.id;
            icon.style.left = width+'px';
            icon.style.top = event.offsetY+'px';
            // icon.innerHTML = target;
            var tmp = this.countries.filter(element => {
                    return element.id == target;
                });
            if(tmp.length > 0){
                icon.children[0].innerHTML = tmp[0].name;
            }
        },
        mouseOffEffect(event){
            var icon = document.getElementById("icon");
            icon.style.display = "none";
        }
    },
}
</script>